<template>
    <section class="pc-container pb-4 px-4">
        <div class="pcoded-content">

            <div class="card">
                <div class="card-header py-3">
                    <div class="d-flex align-items-center">
                        <h5>Lista de Instancias</h5>
                        <button class="btn btn-success btn-sm px-4 ml-auto rounded-1" @click="mostrarModal">
                            Nuevo
                        </button>
                    </div>
                </div>
                <div class="card-body">
                    <div class="table-responsive">
                        <table class="table" id="miTabla1">
                            <thead>
                                <tr>
                                    <th class="text-center pl-4 col-lg-2 col-sm-1">#</th>
                                    <th>Nombre</th>
                                    <th class="text-center">Opciones</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(instancia, index) in instancias" :key="instancia.ins_id">
                                    <td class="text-center py-2 my-0">
                                        <input class="form-check-input me-1" type="checkbox" value="" />
                                    </td>
                                    <td>{{ instancia.ins_nombre }}</td>
                                    <td class="text-center">
                                        <a href="javascript:void(0)" @click="editar(instancia.ins_id)"
                                            class="text-primary px-2 py-1 btn-lg mx-1">
                                            <span class="">
                                                <i data-feather="edit"></i>
                                            </span>
                                        </a>
                                        <a href="javascript:void(0)" @click="borrar(instancia.ins_id)"
                                            class="px-2 py-1 btn-lg text-danger">
                                            <span>
                                                <i data-feather="trash"></i>
                                            </span>
                                        </a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>

            <div v-if="showModal" class="modal" @click="cerrarModal">
                <div class="modal-content card border-0" @click.stop>
                    <div class="card-header">
                        <h5 class="text-center">
                            {{ instancia.ins_id ? 'Actualizar Instancia' : 'Registar Nueva Instancia' }}
                        </h5>
                    </div>
                    <div class="card-body mb-0 pb-0">
                        <div class="row">
                            <div class="col-md-12">
                                <div class="form-group">
                                    <label class="form-label">Nombre</label>
                                    <input class="form-control" v-model="instancia.ins_nombre" @blur="validarNombre"
                                        @input="errores.ins_nombre = false" @keyup.enter="guardar"
                                        :class="{ 'has-error': errores.ins_nombre }">
                                    <span class="error-message" v-if="errores.ins_nombre">Ingrese el nombre de la Instancia.</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="card-footer border-0 mt-0 pt-0">
                        <div class="d-flex justify-content-end">
                            <button class="btn btn-secondary btn-lg  px-4 mx-2 rounded" @click="cerrarModal">Cerrar</button>
                            <button class="btn btn-primary btn-lg  px-4 rounded" @click="guardar">
                                {{ instancia.judis_id ? 'Actualizar' : 'Guardar' }}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import feather from 'feather-icons';
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import { mapActions } from 'vuex';

export default {
    name: "djudiciales",
    created() {
        this.leer();
    },

    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            dataTable: null,
            showModal: false,
            instancias: [],
            instancia: {
                ins_id: "",
                ins_nombre: "",
            },
            errores: {
                ins_nombre: false
            }
        };
    },

    methods: {
        ...mapActions(["get", "post"]),

        validarCampo(campo, propiedadError) {
            if (this.instancia[campo] === "") {
                this.errores[propiedadError] = true;
            } else {
                this.errores[propiedadError] = false;
            }
        },

        validarNombre() {
            this.instancia.ins_nombre = this.instancia.ins_nombre;
            this.validarCampo('ins_nombre', 'ins_nombre');
        },

        validarCampos() {
            this.validarNombre();
            return !Object.values(this.errores).some(error => error);
        },

        leer() {

            this.get({
                url: this.$store.getters.get__url + "/instance",
                token: this.$store.getters.get__token,
            })
                .then((response) => {
                    this.instancias = response.data;
                    this.initDataTable();
                })
                .catch((errors) => { });
        },

        seleccionarDistrito() {
            this.cargarinstancias();
            console.log('distrito-seleccionado');
        },

        async crear() {
            if (!this.validarCampos()) {
                console.error('Falta completar los campos del registro.');
                return;
            }

            try {
                const response = await this.post({
                    url: `${this.$store.getters.get__url}/instance/store`,
                    token: this.$store.getters.get__token,
                    params: {
                        ins_nombre: this.instancia.ins_nombre
                    }
                });

                if (response.state === 0) {
                    await this.leer();
                    this.showModal = false;
                    this.instancia.ins_nombre = "";
                    console.info('instancia creada correctamente');
                } else {
                    console.error('Error en la respuesta del servidor:', response.message);
                }
            } catch (error) {
                console.error('Error en la solicitud:', error);
            }
        },

        async actualizar(id) {
            if (!this.validarCampos()) {
                console.error('Falta completar los campos del registro.');
                return;
            }

            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/instance/update",
                    token: this.$store.getters.get__token,
                    params: {
                        ins_id: id,
                        ins_nombre: this.instancia.ins_nombre
                    }
                });

                if (response.state == 0) {
                    await this.leer();
                    this.showModal = false;
                } else {
                    console.log("error");
                }
            } catch (errors) {
                console.error(errors);
            }
        },

        async borrar(id) {
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/instance/destroy",
                    token: this.$store.getters.get__token,
                    params: {
                        ins_id: id
                    }
                });

                if (response.state == 0) {
                    await this.leer();
                } else {
                    console.log("error");
                }
            } catch (errors) {
                console.error(errors);
            }
        },

        editar(id) {
            const editInstancia = this.instancias.find(instancia => instancia.ins_id === id);
            if (editInstancia) {
                this.instancia = { ...editInstancia };
                this.showModal = true;
            } else {
                console.error("Instancia no encontrado");
            }
        },

        guardar() {
            try {
                if (!this.instancia.ins_id) {
                    this.crear();
                } else {
                    this.actualizar(this.instancia.ins_id);
                }
            } catch (error) {
                console.error(error);
            }
        },

        initDataTable() {
            if (this.dataTable) {
                this.dataTable.destroy();
            }

            this.$nextTick(() => {
                this.dataTable = $('#miTabla1').DataTable({
                    language: {
                        url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
                    },
                });
                feather.replace();
            });
        },

        mostrarModal() {
            this.showModal = true;
        },

        cerrarModal() {
            this.showModal = false;
            this.errores.ins_nombre = false;
            this.instancia.ins_id = "";
            this.instancia.ins_nombre = "";
        }
    },
};
</script>

<style scoped>
td {
    white-space: normal !important;
    word-wrap: break-word;
}

.circle {
    width: 30px;
    height: 30px;
    background-color: #7267EF;
    color: white;
    font-weight: 600;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: #fff;
    width: 500px;
    height: auto !important;
    /* padding: 20px; */
    border-radius: 8px !important;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    position: relative !important;
}

@media (max-width: 768px) {
    .modal-content {
        width: 90%;
    }
}

.card-header {
    border-radius: 8px 8px 0px 0px !important;
}

.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    padding-left: 15px;
    color: #EA4D4D;
    font-size: 12px;
}
</style>
